* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

:root {
  --primary: tomato;
  --text: rgba(0, 0, 0, 0.8);
  --text-secondary: rgba(0, 0, 0, 0.6);
  --text-white: rgb(233, 233, 233);
  --shade: rgb(245, 245, 245);
  --infected: rgba(73, 135, 255, 0.8);
  --recovered: rgba(73, 255, 91, 0.8);
  --deaths: rgba(255, 73, 73, 0.8);
}

body {
  color: var(--text);
  font-size: 16px;
  line-height: 1.6;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
}

a {
  color: var(--primary);
  text-decoration: none;
}

.loading {
  padding: 3em 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100%;
  height: 100%;
}

.section-title {
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 50px;
}

.section-title h1 {
  font-size: 2.25rem;
  margin-bottom: 10px;
}

.section-title hr {
  border: 4px solid var(--text);
  width: 10%;
  margin: auto;
}

.main-section {
  padding: 50px 0;
}

.main-section.shade {
  background: var(--shade);
}

.main-header {
  padding: 20px 0;
}

.main-header .container {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (1fr)[12];
      grid-template-columns: repeat(12, 1fr);
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.main-header .container .brand {
  grid-column: span 3;
}

.main-header .container .brand img {
  height: 40px;
}

.main-header .container .main-nav {
  grid-column: span 9;
}

.main-header .container .main-nav a {
  color: var(--text);
  padding: 10px 15px;
}

.main-header .container .main-nav .toggle-nav {
  display: none;
}

.about-section {
  padding: 0em 1em;
  max-width: 960px;
  margin: 0 auto;
  text-align: center;
}

.countrypicker {
  display: block;
  padding: 5px 10px;
  border-radius: 20px;
  height: 45px;
  width: 90%;
  max-width: 400px;
  outline: none;
  font-size: 1em;
  margin: 0 auto 50px auto;
  border-right: 10px solid transparent;
  -webkit-box-shadow: 0px 0px 1px var(--text);
          box-shadow: 0px 0px 1px var(--text);
}

.diagram {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.diagram .chart {
  width: 70%;
}

.cards {
  display: -ms-grid;
  display: grid;
  grid-gap: 15px;
  justify-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-grid-columns: (minmax(300px, 1fr))[auto-fill];
      grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
}

.card {
  color: white;
  padding: 20px;
  border-radius: 20px;
  width: 100%;
  background: rgba(73, 135, 255, 0.8);
}

.card.infected {
  background: var(--infected);
}

.card.recovered {
  background: var(--recovered);
}

.card.deaths {
  background: var(--deaths);
}

.card .title {
  font-size: 1.125em;
  font-weight: 600;
  text-transform: uppercase;
}

.card .cases {
  font-size: 2.25em;
  margin-bottom: 10px;
}

.card .date {
  color: var(--text-white);
  font-weight: normal;
}

.main-footer {
  text-align: center;
  padding: 20px 0px;
}

.main-footer h4 {
  color: var(--text-secondary);
  font-size: 0.875em;
}

@media (max-width: 768px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
  .main-header {
    position: relative;
    padding: 10px 0;
  }
  .main-header .container {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: (1fr)[12];
        grid-template-columns: repeat(12, 1fr);
    padding-left: 20px;
    padding-right: 20px;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
  }
  .main-header .container .brand {
    grid-column: span 6;
  }
  .main-header .container .brand img {
    height: 30px;
  }
  .main-header .container .main-nav {
    grid-column: span 6;
  }
  .main-header .container .main-nav .items {
    position: fixed;
    top: 0;
    left: 0;
    min-height: 100vh;
    width: auto;
    padding: 2em 4em 2em 1em;
    background: rgba(255, 255, 255, 0.98);
    display: block;
    z-index: 100;
  }
  .main-header .container .main-nav .items.hide {
    display: none;
  }
  .main-header .container .main-nav .items::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.98);
    z-index: -1;
  }
  .main-header .container .main-nav a {
    display: block;
    color: var(--text);
    padding: 10px 15px;
  }
  .main-header .container .main-nav .toggle-nav {
    width: 100%;
    text-align: right;
    display: block;
    font-size: 24px;
    border: none;
    outline: none;
    background: transparent;
    line-height: 1;
  }
  .diagram .chart {
    width: 100%;
  }
  .section-title {
    margin-bottom: 30px;
  }
  .section-title h1 {
    font-size: 2rem;
  }
  .section-title hr {
    border: 2px solid var(--text);
    width: 20%;
  }
  .main-footer h4 {
    font-size: 0.75em;
  }
}
